import { Card, Tabs } from 'antd';
import { useEffect, useReducer, useState } from 'react';
import { CronDay, CronHours, CronMinutes, CronMonth, cronReducer, CronSeconds, CronYear } from './CronExpression';
import './style.scss'


const QuartzString = () => {
    const [resultString, setResultString] = useState('');
    const [state, dispatch] = useReducer(cronReducer, {
        seconds: '0',
        minutes: '0',
        hours: '0',
        day: '*',
        month: '*',
        year: '*',
        symbol: '?',
        mode: 'default'
    });

    useEffect(() => {
        updateResultString();
    }, [state])

    function updateResultString() {
        let str = '';
        switch (state.mode) {
            case 'default':
                str = `${state.seconds} ${state?.minutes} ${state?.hours} ? ${state?.month} ${state?.day} ${state?.year}`;
                break
            case 'month':
                str = `${state?.seconds} ${state?.minutes} ${state?.hours} ${state?.day} ${state?.month}  ? ${state?.year}`;
                break
            default:
                str = '';
                break
        }
        setResultString(str);
    }

    const tabList: any = [
        {
            title: 'Seconds',
            TabContent: CronSeconds,
            extraProps: dispatch
        },
        {
            title: 'Minutes',
            TabContent: CronMinutes,
            extraProps: dispatch
        },
        {
            title: 'Hours',
            TabContent: CronHours,
            extraProps: dispatch
        },
        {
            title: 'Day',
            TabContent: CronDay,
            extraProps: dispatch
        },
        {
            title: 'Month',
            TabContent: CronMonth,
            extraProps: dispatch
        },
        {
            title: 'Year',
            TabContent: CronYear,
            extraProps: dispatch
        },
    ]

    return (
        <div style={{ width: '100%', height: '100vh', padding: '25px 0', backgroundColor: '#ccc' }}>
            <div style={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
                <Card className='myCard' style={{ margin: '0 auto', overflowY: 'auto', backgroundColor: 'white', height: '100%', width: '80%' }}>
                    <Tabs defaultActiveKey="1" type="card" >
                        {
                            tabList.map((item: any) => {
                                return <Tabs.TabPane tab={item.title} key={item.title}>
                                    <item.TabContent extraProps={item.extraProps} />
                                </Tabs.TabPane>
                            })
                        }
                    </Tabs>
                    <Card>
                        <h4 style={{ wordWrap: 'break-word', fontSize: '32px' }}>
                            {resultString}
                        </h4>
                    </Card>
                </Card>
                <a style={{ textAlign: 'center', display: 'inline-block', width: '100%', marginTop: '20px' }} href="https://www.freeformatter.com/cron-expression-generator-quartz.html">
                    :FREEFORMATTER.COM</a>
            </div>

        </div>
    );
}

export default QuartzString;